import { Injectable } from '@angular/core';
import {  Http,Response  } from '@angular/http';

import { Observable } from 'rxjs';

import 'rxjs/add/observable/of';
import 'rxjs/add/observable/throw';

// Observable operators
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/switchMap';

import { IRating, IRank, IMemberRating } from './interfaces/IRating';

@Injectable()
export class StatsService {


    private membersUrl = 'api/members';
    private statsUrl = 'api/stats';

    constructor(private http: Http) { }

    getMembers(): Observable<string[]> {
        console.log('getMembers called');
        return this.http.get(this.membersUrl)
            .map((response: Response) => <string[]>response.json())
            .catch(this.handleError);
    }

    getStats(): Observable<IMemberRating[]> {
        console.log('getStats called');
        return this.http.get(this.statsUrl)
            .map((response: Response) => <IMemberRating[]>response.json())
            .catch(this.handleError);
    }

    private handleError(error: any): Promise<any> {
        console.error('An error occurred', error); // for demo purposes only
        return Promise.reject(error.message || error);
    }
}


