import { Component, OnInit } from '@angular/core';

import { StatsService } from './stats.service';
import { IRating, IRank, IMemberRating } from './interfaces/IRating';

var dateFormat = require('dateformat');

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {


  stats: IMemberRating[];

  options: Object;

  values:string[]=[];

  public dataSet: any[] = [
    { data: [], label: '' }
  ];

  public dates: string[] = [];

  constructor(private statsService: StatsService) { }

  ngOnInit(): void {
    this.getStats();
  }

  getStatsOptions(): void {
    let rating = this.stats[0].ratings[0];
    for (var i in rating) {
      if (rating.hasOwnProperty(i)&& !(i==='date' || i==='account_id' || i==='battles_to_play')) {
        this.values.push(i);
      }
    }
  };

  getStats(): void {
    this.statsService.getStats()
      .subscribe((data: IMemberRating[]) => this.stats = data,
      error => console.log(error),
      () => {
        console.log('Get Stats complete');
        this.getStatsOptions();
        this.getDates();

        this.select(this.values[0]);

      });
  };

  updateChartData(): void {
    this.options = {
      title: { text: this.title },
      series: this.dataSet,
      xAxis: {
        categories: this.dates
      },
      yAxis: {
        title: {
          text: 'Werte'
        }
      }
    };
  }


  getDataset(statName: string): void {
    let dataset = [];

    for (let memberRating of this.stats) {
      let numbers: number[] = [];
      for (let rating of memberRating.ratings.sort(this.sortRatings)) {
        if (Object.prototype.hasOwnProperty.call(rating, statName)) {
          let rank: IRank = rating[statName];
          console.log(rank);
          numbers.push(rank.value);
        }
      }
      dataset.push({ data: numbers, name: memberRating.member.account_name, allowPointSelect: true });
    }
    this.dataSet = dataset;
    console.log(this.dataSet);
  }

  getDates(): void {
    this.dates = [];
    let tmpDates: string[] = [];
    for (let rating of this.stats[0].ratings.sort(this.sortRatings)) {
      tmpDates.push(dateFormat(new Date(rating.date * 1000), 'dd mm yy'));
    }
    console.log(tmpDates);
    this.dates = tmpDates;

  }

  sortRatings(r1: IRating, r2: IRating): number {
    if (r1.date > r2.date)
      return 1;
    if (r1.date < r2.date)
      return -1;
    return 0;
  }

  select(value:string){
    this.title=value;
    this.getDataset(value);
    this.updateChartData();
  }

  title = '';




}



